import React from 'react';
import './Anim_CH_MOV_5.scss';

import { connect } from 'react-redux';
import AShowCard from '../../../../../store/actions/AShowCard';

class Anim_CH_MOV_5 extends React.Component {
    constructor ( props ) {
        super ( props );
        this.state = {
            words : [
                //прыметнік
                [
                    "высокі", "разумны", "прыгожы", "зялёны", "сіні", "ружовы", "чырвоны", 
                    "белы", "глыбокі", "шаноўны", "паважаны", "далёкі", "блізкі", "малы",
                    "нізкі", "чорны", "страшэнны", "пачварны", "прывабны", "лёгкі", "мяккі",
                    "цяжкі", "школьны", "бацькоўскі", "дзіцячы", "матулін", "жорсткі", "смелы",
                    "адважны", "труслівы", "ганебны", "прыкры", "непрыемны", "мужны"
                ],
                //назоуник
                [
                    "дом", "сцяна", "дзеці", "настаўнік", "школа", "машына", "вучні", "дошка",
                    "настаўніцкая", "камп’ютар", "барацьба", "дзядзька", "снег", "сняжынка", "плот",
                    "сурвэтка", "кажан", "кавун", "гарбуз", "вусень", "слімак", "абрус", "гасціннасць",
                    "шчасце", "бяда", "любоў", "каханне", "недавер", "бусел", "шпак", "лыжы", "бігудзі",
                    "кансервы", "нож", "відэлец", "сукенка", "штаны", "восень", "канікулы", "пяцёрка"
                ],
                //дзеяслоу	
                [
                    "хадзіць", "пісаць", "чытаць", "ляжаць", "глядзець", "хварэць", "гаварыць", "любіць",
                    "кахаць", "адказваць", "маляваць", "ненавідзець", "маўчаць", "скакаць", "спаць",
                    "працаваць", "ехаць", "стаяць", "падкрэсліваць", "часаць", "бубніць", "спяваць",
                    "набіраць", "карміць", "кашляць", "выздараўліваць", "радавацца", "будаваць",
                    "думаць", "старацца", "працягваць", "імкнуцца", "лятаць", "шаптаць", "крычаць",
                    "гуляць"
                ],
                //займенник
                [
                    "я", "нас", "мы", "нехта", "хтосьці", "сябе", "вы", "ніхто", "нішто", "нешта",
                    "увесь", "ты", "яны", "сам", "усякі", "мой", "твой", "іншы", "кожны", "ваш"
                ]
            ],
            curSet : [],
            curIndex : 0,
            curWord : "",
            rightCount : 0
        }
        
        this.setArr  = this.setArr.bind( this );
        this.ShuffleArr = this.ShuffleArr.bind( this );
        this.wordClick = this.wordClick.bind( this );
    }
    setArr() {
        let bigArr = this.state.words[0].concat(this.state.words[1]);
        bigArr = bigArr.concat(this.state.words[2]);
        bigArr = bigArr.concat(this.state.words[3]);
        bigArr = this.ShuffleArr( bigArr );
        this.setState( (state) =>({
            ...state, 
            curSet : [ bigArr[0],bigArr[2],bigArr[4],bigArr[6],bigArr[8],
                       bigArr[11],bigArr[13],bigArr[15],bigArr[17],bigArr[19], ],
            curWord : bigArr[0]
        }))
    }
    ShuffleArr( array ) {        
        return array.sort(() => Math.random() - 0.5);
     }
    componentDidMount() {
        this.setArr();
    }
    wordClick ( xx ) {
        let wordType = -1;
        if ( this.state.words[0].indexOf( this.state.curWord ) != -1 ) wordType = 0 ;
        if ( this.state.words[1].indexOf( this.state.curWord ) != -1 ) wordType = 1 ;
        if ( this.state.words[2].indexOf( this.state.curWord ) != -1 ) wordType = 2 ;
        if ( this.state.words[3].indexOf( this.state.curWord ) != -1 ) wordType = 3 ;      
        if ( xx == wordType ) {
            this.setState( (state) => ({
                ...state,
                rightCount : state.rightCount+1
            }))
            document.getElementsByClassName("Anim_CH_MOV_5__rezPanel__circle")[this.state.curIndex].style.backgroundColor = "green"; 
        }         
        else {
            document.getElementsByClassName("Anim_CH_MOV_5__rezPanel__circle")[this.state.curIndex].style.backgroundColor = "red"; 
        }
        if ( this.state.curIndex < 9 ) {
            this.setState( (state) =>({
                ...state, 
                curIndex : state.curIndex + 1,
                curWord : state.curSet[ state.curIndex +1 ]
            }))
        }
        else {
            alert ( "Ваш вынік : " +this.state.rightCount + " з 10" );
            this.props.GoToAnimMenu();
        }
    }
    render() {
        return (
            <div className = "Anim_CH_MOV_5">
                <div className = "Anim_CH_MOV_5__rezPanel">
                    <div className = "Anim_CH_MOV_5__rezPanel__circle"></div>
                    <div className = "Anim_CH_MOV_5__rezPanel__circle"></div>
                    <div className = "Anim_CH_MOV_5__rezPanel__circle"></div>
                    <div className = "Anim_CH_MOV_5__rezPanel__circle"></div>
                    <div className = "Anim_CH_MOV_5__rezPanel__circle"></div>
                    <div className = "Anim_CH_MOV_5__rezPanel__circle"></div>
                    <div className = "Anim_CH_MOV_5__rezPanel__circle"></div>
                    <div className = "Anim_CH_MOV_5__rezPanel__circle"></div>
                    <div className = "Anim_CH_MOV_5__rezPanel__circle"></div>
                    <div className = "Anim_CH_MOV_5__rezPanel__circle"></div>
                </div>
                <p className = "Anim_CH_MOV_5__word">{ this.state.curWord }</p>
                <div className = "Anim_CH_MOV_5__but__panel">
                    <div 
                        className = "Anim_CH_MOV_5__but"
                        onClick = { (x) => this.wordClick(0) }>Прыметнік</div>
                    <div 
                        className = "Anim_CH_MOV_5__but"
                        onClick = { (x) => this.wordClick(1) }>Назоўнік</div>
                    <div 
                        className = "Anim_CH_MOV_5__but"
                        onClick = { (x) => this.wordClick(2) }>Дзеяслоў</div>
                    <div 
                        className = "Anim_CH_MOV_5__but"
                        onClick = { (x) => this.wordClick(3) }>Займеннік</div>
                </div>
            </div>
        )
    }
}

let mapState = state => {
    return {

    }
}

    let mapDispatch = dispatch => {
        return {
            GoToAnimMenu : ( ) =>dispatch ( AShowCard( "Анімацыі" ) )            
    }
}

export default connect (mapState, mapDispatch) (Anim_CH_MOV_5);