import React from 'react';
import './Principes.scss';

import { connect } from 'react-redux';
import AShowCard from '../../../../../store/actions/AShowCard';

class Principes extends React.Component {
    constructor ( props ) {
        super ( props );
        this.state = {
            words : [[
                "дождж",
                "барацьба",
                "мяккі",
                "лёгкі",
                "смяешся",
                "снег",
                "матчын",
                "на дошцы",
                "просьба",
                "вакзал",
                "футбол",
                "лічба",
                "грамадства",
                "суседскі",
                "не бойся",
                "мыешся",
                "не бойся",
                "дзверы",
                "песня", 
                "семестр",
                "цвёрды",
                "бясшумны",
                "грыб",
                "лодка",
                "зжаць",
                "французскі"
            ],
            [
                "зямля",
                "дом",
                "галава",
                "сонца",
                "балота",
                "дым",
                "узвышша",
                "стагоддзе",
                "навука",
                "пісаць",
                "бары",
                "заказаць",
                "клас",
                "ліст",
                "траўка",
                "спявак",
                "абыграць",
                "быццам",
                "ляснік",
                "нос",
                "том",
                "літара",
                "ежа",
                "вусень",
                "бор",
                "гаворка",
                "клён",
                "удар"
            ]],
            curSet : [],
            curIndex : 0,
            curWord : "",
            rightCount : 0
        }
        this.setArr  = this.setArr.bind( this );
        this.ShuffleArr = this.ShuffleArr.bind( this );
        this.sentClick = this.sentClick.bind( this );
    }
    setArr() {
        let bigArr = this.state.words[0].concat(this.state.words[1]);        
        bigArr = this.ShuffleArr( bigArr );
        this.setState( (state) =>({
            ...state, 
            curSet : [ bigArr[0],bigArr[2],bigArr[4],bigArr[6],bigArr[8],
                       bigArr[11],bigArr[13],bigArr[15],bigArr[17],bigArr[19], ],
            curWord : bigArr[0]
        }))
    }
    ShuffleArr( array ) {        
        return array.sort(() => Math.random() - 0.5);
     }
    componentDidMount() {
        this.setArr();
    }
    sentClick ( xx ) {
        let wordType = -1;
        if ( this.state.words[0].indexOf( this.state.curWord ) != -1 ) wordType = 0 ;
        if ( this.state.words[1].indexOf( this.state.curWord ) != -1 ) wordType = 1 ;            
        if ( xx == wordType ) {
            this.setState( (state) => ({
                ...state,
                rightCount : state.rightCount+1
            }))
            document.getElementsByClassName("Principes__rezPanel__circle")[this.state.curIndex].style.backgroundColor = "green"; 
        }         
        else {
            document.getElementsByClassName("Principes__rezPanel__circle")[this.state.curIndex].style.backgroundColor = "red"; 
        }
        if ( this.state.curIndex < 9 ) {
            this.setState( (state) =>({
                ...state, 
                curIndex : state.curIndex + 1,
                curWord : state.curSet[ state.curIndex +1 ]
            }))
        }
        else {
            alert ( "Ваш вынік : " +this.state.rightCount + " з 10" );
            this.props.GoToAnimMenu();
        }
    }
    render() {
        return (
            <div className = "Principes">
                <div className = "Principes__rezPanel">
                    <div className = "Principes__rezPanel__circle"></div>
                    <div className = "Principes__rezPanel__circle"></div>
                    <div className = "Principes__rezPanel__circle"></div>
                    <div className = "Principes__rezPanel__circle"></div>
                    <div className = "Principes__rezPanel__circle"></div>
                    <div className = "Principes__rezPanel__circle"></div>
                    <div className = "Principes__rezPanel__circle"></div>
                    <div className = "Principes__rezPanel__circle"></div>
                    <div className = "Principes__rezPanel__circle"></div>
                    <div className = "Principes__rezPanel__circle"></div>
                </div>
                <p className = "Principes__word">{ this.state.curWord }</p>
                <div className = "Principes__but__panel">
                    <div 
                        className = "Principes__but"
                        onClick = { (x) => this.sentClick(0) }>марфалагічны</div>
                    <div 
                        className = "Principes__but"
                        onClick = { (x) => this.sentClick(1) }>фанетычны</div>                   
                </div>
            </div>
        )
    }
}

let mapState = state => {
    return {

    }
}

    let mapDispatch = dispatch => {
        return {
            GoToAnimMenu : ( ) =>dispatch ( AShowCard( "Анімацыі" ) )            
    }
}

export default connect (mapState, mapDispatch) (Principes);