import React from 'react';
import './index.scss';

class Anim_8_ZPT extends React.Component {
    constructor ( props ) {
        super ( props );
        this.state = {
            questArr : [
                "Я спыніўся, аслеплены сонцам, толькі на ўзлеску, перад полем.",
                "Захопленыя сваімі гульнямі, мы і не заўважылі, як сонца схілілася да самага лесу.",
                "Ахінутыя інеем, прыгіналіся да самай зямлі кусты малінніку і парэчак.",
                "На паляне, пакрытай буйнымі кроплямі расы, раслі дзве бярозкі.",
                "Ноч, ціхая, цёплая, ахутала змрокам зямлю.",
                "Высокая хата, з цагляным падмуркам, з чарапічным дахам, была адной з самых прыкметных у вёсцы.",
                "Над зялёным лугам, усыпаным рознымі кветкамі, гулі пчолы.",
                "У вячэрнім паветры, крыху вільготным ад блізкай ракі, прыемна пахне шыпшынай.",
                "Вёска стаяла каля балота, парослага густою лазою і алешнікам.",
                "Сена, змочанае даждом, запахла яшчэ мацней.",
                "Пакрытыя тоўстым пластом снегу, галіны дрэў гнуліся да самай зямлі.",
                "Ходзячы па моху, Платон пачаў натрапляць на баравікі.",
                "Вада, мутная, цяжкая, ціха білася аб бераг, калыхала прыбярэжныя кусты.",
                "Рака, спакойная ўчора, уночы выйшла з берагоў.",
                "Трава, рослая, густая, валілася на бок тугім валам.",
                "Смуга, тонкая, сіняватая, песціла лясы і лугі.",
                "Вядомы майстар, Васіль Іванавіч застаўся сціплым чалавекам.",
                "Піліп, па мянушцы Лысы, сядзеў на лаўцы свайго дзеда.",
                "Сын лесніка, я сам з маленства любіў блукаць па лесе, назіраць за птушкамі, жывёламі.",
                "На свеце ўсё знойдзеш, апрача бацькі і маці.",
                "Сёння Міхась дзяжурыў замест сябра.",
                "Многія, асабліва мужчыны, так і не вярнуліся з вайны.",
                "Ніхто, нават вецер, не кранае галін яблынь і груш у белай квецені.",
                "Уперадзе стаяў лясок, пераважна хваёвы.",
                "Лес быў пераважна бярозавы.",
                "У Арыны, апрача яе вонкавай прыгажосці, было нешта сваё.",
                "Увесь месяц, за выключэннем пяці-сямі дзён, ішлі халодныя дажджы.",
                "У лесе, апрача чарніц, было многа суніц.",
                "Раскінуўшы крылы, чайка планіруе ў паветры, выглядаючы ў вадзе сабе здабычу.",
                "То скачучы з купіны на купіну, то павольна ідучы па вузкіх сцежках, доўга хадзіў Яўген па балоце.",
                "Кіраўнікі дзяржаў, падпісаўшы дагавор, правялі прэс-канферэнцыю.",
                "Не змаўкаючы, моцна гула завіруха.",
                "Набраўшыся смеласці, можна лёгка выканаць нават складаную работу.",
                "Не зважаючы на дождж, мы шпарка крочылі па лясной дарозе.",
                "Паабедаўшы, касцы паселі ў цень пад разложыстым дубам.",
                "Наўкол пляменнікі сязелі, на дзядзьку пільна ўсе глядзелі, як ён, рукавы закасаўшы, па тарцы шоргаў, шапку зняўшы.",
                "Нагаварыўшыся, жанчыны разышліся па сваіх дварах.",
                "Хутка хмара, аддаўшы зямлі належнае, пасвятлела і разарвалася на шматкі.",
                "Воўк авечку бярэ не выбіраючы.",
                "Песні салаўіныя льюцца не сціхаючы.",
                "Трэба быць аптымістам і жыць не апускаючы рук.",
                "Не прывыкла мая бабуля сядзець склаўшы рукі.",
                "Возьме наш хлопец жалеза кавалак ды, кінуўшы ў печ, нагрэе ўдала.",
                "Сонца падымалася вышэй, рупліва аглядаючы зямлю і рассыпаючы над ёю святло і цеплыню.",
                "І ў садзе, і на рацэ стаяла дзівосная цішыня.",
                "Дзесьці высока-высока, за самымі хмарамі, курлыкалі журавы.",
                "Там, удалечыні, каля лесу, раскінулася прырэчная лагчына.",
                "А было гэта тыдзень таму, у нядзелю, пад вечар.",
                "Там, за дуброваю, жнейкі звоняць сярпамі сталёвымі.",
                "Воддаль, пад стромкімі бярозамі, цэлымі сямейкамі растуць мухаморы.",
                "Будынак упрыгожылі хораша, па-святочнаму.",
                "Тут, за рэчкай, адразу пачыналася поле.",
                "Учора, раніцай, дзеці хадзілі ў грыбы.",
                "Мілена гаварыла хутка, слова за словам.",
                "Там, за поплавам, на пагорку, купалася ў сонечным святле вёска.",
                "Тут, у вас, я штодзень хаджу ў кавярню.",
                "Толькі тут, на вузкай сцежцы, я змог добра разгледзець рэшткі гэтага дома-палаца Стругача.",
                "Узыходзіла, прабіваючыся праз імглу, сонца.",
                "У багатым палацы, абкружаным мноствам лёкаяў, жыве Сын Шчасця.",
                "На станцыю павёз Архіпа той жа сват, сівы дзед Яхім.",
                "Летам, асабліва ў ліпені, двор пусцеў, зарастай травою, маліннікам.",
                "Людзі жылі як гарох пры дарозе, людзі жылі ў штодзённай трывозе.",
                "Карына як язык праглынула.",
                "У гэтыя хвіліны яна была для яго як маці.",
                "Патрэбна мне гэтая гульня «Blizzard» як леташні снег.",
                "Не паспеў  Клім падумаць, як перад ім як з зямлі вырас механік іх карабля.",
                "Па камянях, нібы тая змейка, вілася Ашмянка.",
                "Над Ашмянамі, прыбраная ў зоры, апускаецца ноч.",
                "Настаўнік, стаміўшыся, прысеў на крэсла.",
                "На ўзгорку, між кустоўя і дрэў, выглядалі рэшткі  кляштару францішканаў.",
                "Дзеці з нецярпеннем чакалі бацьку і прыслухоўваліся да кожнага кроку ў двары.",
                "Славамір зацікавіўся біялогіяй і стаў наведваць батанічны сад.",
                "Абуўшыся, шчаслівы Герман, выскачыў з хаты.",
                "Дзед замоўк як мыла з’еўшы.",
                "Ян быў ні рыба ні мяса.",
                "Падсілкаваўшыся, Марк рушыў далей, яшчэ да вечара мінуў балота.",
                "Я вярнуўся ў хату і, на запальваючы бра, сеў на ложак.",
                "Вярнуўшыся ў палатку, Эдгар, не запальваючы ліхтарык, пачаў шукаць планшэт.",
                "Нягледзячы на трывожны час, рынак жыў сваім звычайным жыццём.",
                "Вучні пайшлі ў паход нягледзячы на дрэннае надвор’е.",
                "Гэта зрабіў Міраслаў нягледзячы на забарону бацькоў.",
                "Добра ў садзе хадзіць, сустракаючы сонечны ранак.",
                "Рана ўстаўшы, многа зробіш.",
                "Мілана сядзела, падпёршы рукою шчаку, і ўлюбёна назірала за настаўнікам.",
                "Не змаўкаючы, моцна гула мяцеліца.",
                "Пагойдваючыся ад ветру, моцна скрыпелі галінкі."
            ],
            maxQuest : 5,
            curArr : [ ],
            curInd : 0,  
            rightAnsw : 0,
            curSent : ""
        }
        this.ShuffleArr = this.ShuffleArr.bind( this );
        // this.SpanClick  = this.SpanClick.bind( this );
        this.butClick   = this.butClick.bind( this );
        this.ShowQuest  = this.ShowQuest.bind( this );    
        this.retSpan    = this.retSpan.bind ( this );  

    } 
    ShuffleArr( array ) {        
        array.sort(() => Math.random() - 0.5);
    }
    componentDidMount() {
        document.getElementById("Sentence").inneHTML = this.ShowQuest("asdasd adasdas,sad");                          
    }   
    componentWillMount() {
        this.setState( (state)=> ({
            ...state,
            questArr : this.ShuffleArr( state.questArr )
        }));
        this.setState (( state) => ({
            ...state,
            curArr : [ this.state.questArr[0],this.state.questArr[1],this.state.questArr[2],this.state.questArr[3],this.state.questArr[4] ]
        }));   
    }
    retSpan () {
        return (`<span id = "ss">___</span>`)
    }
    ShowQuest( sent ) {
        sent = sent.replace(/,/g, '@')
        sent = sent.replace(/ /g, "#");
        sent = sent.replace(/@#/g, "@");
        
        sent = sent.replace(/#/g, `<span onClick = { ()=>alert() }> 1 </span>`);
        sent = sent.replace(/@/g, `<span onClick = { alert() }> 1 </span>`);

        return sent;
    }
    
    butClick() {
        alert( this.state.curArr[0]);
    }
    render() {
        return (
            <div className = "Anim">
                <p id = "Sentence"></p>            
                <input 
                    id = "but"
                    type = "submit"
                    onClick = { this.butClick }
                    value = "Адказаць"/>
                <div id = "rezCircles">
                    <div className = "rezCircle"></div>
                    <div className = "rezCircle"></div>
                    <div className = "rezCircle"></div>
                    <div className = "rezCircle"></div>
                    <div className = "rezCircle"></div>
                </div>
            </div>
        )
    }
}

export default Anim_8_ZPT;






