import React from 'react';
import './Animations.scss';

import { connect } from 'react-redux';
import AShowCard from '../../../store/actions/AShowCard';

class Animations extends React.Component {
    constructor ( props ) {
        super ( props );
        this.state = {
            animArr : [
                {
                    "title" : "Часціны мовы",
                    "class" : "5 клас",
                    "path"  : "Anim_CH_MOV_5",
                    "back"  : "backImg0"
                },
                {
                    "title" : "Тыпы сказаў",
                    "class" : "любы клас",
                    "path"  : "Anim_sents_type",
                    "back"  : "backImg0"
                },
                {
                    "title" : "Прынцып будовы",
                    "class" : "любы клас",
                    "path"  : "Principes",
                    "back"  : "backImg0"
                }
                
            ]
        }
    }
    ShowAnim (x) {
        this.props.ShowAnim(x)
    }
    render() {
        return (
            <div className = "Animations">                
                    { this.state.animArr.map( (el) => (
                        <div 
                            className = "Animations_Anim"
                            onClick = { (xx) => {this.ShowAnim (el.path)} }>
                            <p> {el.title} </p>
                            <p> {el.class} </p>
                        </div>
                    ))}                
            </div>
        )
    }
}
let mapState = state => {
    return {

    }
}

    let mapDispatch = dispatch => {
        return {
            ShowAnim : ( qq ) =>dispatch ( AShowCard( qq ) )            
    }
}

export default connect( mapState, mapDispatch) (Animations);