import React from 'react';
import './Prisl_0.scss';

import { connect } from 'react-redux';
import AShowCard from '../../../../store/actions/AShowCard';

class Prisl_0 extends React.Component {
    constructor( props ) {
        super ( props );
        this.state = {
            quests : [
                "Адзнач прыслоўе, якое пішацца разам",
                "Адзнач словазлучэнне, у якім на месцы пропуску трэба пісаць літару і",
                "Адзнач словазлучэнне, у якім прыслоўе пішацца з часціцай НЕ асобна",
                "Адзнач прыслоўе, у якім правільна пастаўлены націск",
                "Адзнач блізкае да прыслоўя спалучэнне, якое пішацца асобна",
                "Адзнач слова, якое пішацца праз злучок"
            ],
            answers : [
                ["вывучыць (на) памяць", "зрабіць (па) вашаму", "(у) першыню ўбачыў"],
                ["'н...калі сустракаліся", "н...дзе зусім побач", "н...дзе не відаць"],
                ["зраблю (не) сёння", "(не) абходна зрабіць", "(не) таропка ісці"],
                ["накарміць дасытА", "крыхУ паспаць", "стаялі абaпAл"],
                ["(як) раз насупраць", "адказаць (на) хаду", "разбураны (да) шчэнту"],
                ["(па) ціху","(на) заўтра", "(абы) куды"]
            ],
            rightAnsers : [
                "(у) першыню ўбачыў",
                "н...дзе не відаць",
                "зраблю (не) сёння",
                "крыхУ паспаць",
                "адказаць (на) хаду",
                "(абы) куды"   
            ],
            curIndex : 0,
            curQuest : "",
            curAnswers : [],
            ch0 : false,
            ch1 : true, 
            ch2 : false,
            curSelected : "",
            rightCount : 0
        }
        this.SetCurQuest = this.SetCurQuest.bind( this );
        this.chClick     = this.chClick.bind( this );     
        this.ShuffleArr  = this.ShuffleArr.bind ( this );  
        this.GoToTestMenu = this.GoToTestMenu.bind ( this );

    }
    SetCurQuest() {    
        if ( this.state.ch0||this.state.ch1||this.state.ch2) {
            console.log ( this.state.curSelected === this.state.rightAnsers[this.state.curIndex -1] );
            if ( this.state.curSelected === this.state.rightAnsers[this.state.curIndex -1] ) {                
                this.setState((state) => ({
                    ...state,
                    rightCount : state.rightCount + 1
                }))
            }            
            this.setState( (state) => ({
                ...state, 
                ch0 : false, 
                ch1 : false, 
                ch2 : false
            }))
            let ind = this.state.curIndex;
            if ( ind < 5 ) {
                let arr = [this.state.answers[ind][0], this.state.answers[ind][1], this.state.answers[ind][2]];
                arr = this.ShuffleArr( arr );
                this.setState( (state) => ({
                    ...state,            
                    curQuest : state.quests[ind],
                    curAnswers : [ arr[0], arr[1], arr[2] ],
                    curIndex : ind + 1
                }))
            }   
            else {
                alert ("Дакладна "+this.state.rightCount + " з 5" );
                this.GoToTestMenu();
            }  
        }   
        else {
            alert ( "Неабходна выбраць адказ")
        }
          
    }
    ShuffleArr( array ) {        
       return array.sort(() => Math.random() - 0.5);
    }
    chClick( x ) {
        switch ( x ) {
            case 0 : 
               this.setState( (state) => ({
                   ...state,
                   ch0 : true, ch1: false, ch2: false, curSelected : state.curAnswers[0]
               })); break;               
            case 1 : 
               this.setState( (state) => ({
                   ...state,
                   ch0 : false, ch1: true, ch2: false, curSelected : state.curAnswers[1]
               })); break;   
            case 2 : 
               this.setState( (state) => ({
                   ...state,
                   ch0 : false, ch1: false, ch2: true, curSelected : state.curAnswers[2]
               })); break;   
        }
    }   
    componentDidMount() {
        this.SetCurQuest();
    }
    GoToTestMenu() {
        this.props.GoToTestMenu();
    }
    render() {
        return (
            <div className = "Prisl_0">
                <p className = "P0_quest">{ this.state.curQuest }</p>
                    <div>
                    <input 
                        type = "checkbox"
                        name = "q1"
                        id = "q1"
                        checked = { this.state.ch0 }
                        onChange = { (x) => this.chClick(0) }                       
                        ></input>
                    <label htmlFor="q1" className = "P0_ans">{ this.state.curAnswers[0] }</label>
                    </div>

                    <div>
                    <input 
                        type = "checkbox"
                        name = "q1"
                        id = "q2"
                        checked = { this.state.ch1 } 
                        onChange = { (x) => this.chClick(1) }></input>
                    <label htmlFor="q2" className = "P0_ans">{ this.state.curAnswers[1] }</label>
                    </div>

                    <div>
                    <input 
                        type = "checkbox"
                        name = "q1"
                        id = "q3"
                        checked = { this.state.ch2 } 
                        onChange = { (x) => this.chClick(2) }></input>
                    <label htmlFor="q3" className = "P0_ans">{ this.state.curAnswers[2] }</label>
                    </div>
                <input
                    type = "submit"
                    value = "Адказаць"
                    onClick = { this.SetCurQuest }></input>
            </div>
        )
    }
}


let mapState = state => {
    return {

    }
}

    let mapDispatch = dispatch => {
        return {
            GoToTestMenu : ( ) =>dispatch ( AShowCard( "Тэсты" ) )            
    }
}

export default connect(mapState, mapDispatch) (Prisl_0);

